*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content{
  height: 80vh;
}
.navbar{
  background-color: white;
 
}
.register-page{
  background-color: lightblue;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  
}

.form{
  width: 50vh;
}
.heading{
  text-align: center;
  font-size: 60px;
  font-family: Arial, Helvetica, sans-serif;
}
.input{
  height: 50px;
  border-radius: 25px;
  font-size: 15px;
  padding: 25px;
}
.send{
  text-align: center;
  
}

.btn{
  border-radius: 25px;
  width: 20vh;
  
}
.send{
  justify-content: right;
  margin-right:15px ;
}
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  color: #f1f1f1;
  background-color: #282A35;
  margin: 5px 10px 0px 10px ;
  border-bottom-right-radius: 7px;
  border-bottom-left-radius: 7px;
}
.content{
  background-color: skyblue;
  padding: 5px 10px 0px 10px;
}
.expense{
  padding: 2px 10px;
}

.row{
  max-width: 400px;
  
}
.anticon svg{
  font-size:20px;
  cursor: pointer;
}
.switch-icons{
  border: 1px solid white;
  border-radius: 5px;
  padding: 10px 20px;
}
.active-icon{
  color:white;
}
.inactive-icon{
  color: white;
}


